<style lang="less" scoped>
.map {
  width: 100%;
  height: 400px;
}
</style>
<template>
  <div class="realtime">
   <baidu-map class="map" :center="center" :zoom="zoom" @ready="handler"></baidu-map>
  </div>
</template>
<script>
  export default {
    name: 'realtime',
    data () {
      return {
        center: {lng: 0, lat: 0},
        zoom: 3
      }
    },
    methods: {
      handler ({BMap, map}) {
        console.log(BMap, map)
        this.center.lng = 116.404
        this.center.lat = 39.915
        this.zoom = 15
      }
    }
  }
</script>


